export default {
  props: {
    src: {
      type: String,
    },
    mode: {
      type: String,
    },
    class: {
      type: String,
    },
    headerImg: {
      type: String,
    },
    type: {
      type: String,
    },
    disWithHost: {
      type: Boolean,
    },
  },
  data() {
    return {
      isLoadingImg: true,
    };
  },
  created() {
    console.info("1212", "");
  },
  methods: {
    imgLoadSuccess(e) {
      this.isLoadingImg = false;
      this.imgLoadError = false;
    },
    imgLoadError(e) {
      console.error("imgLoadError", e);
      
      this.isLoadingImg = false;
      this.imgLoadError = true;
    },
  },
};
